import axios from 'axios';

const url = 'api/';

class DataService {

    static getDrunk() {
        return new Promise ((resolve,reject) => {
            axios.get(url+ "drunk").then((res) => {
                resolve(res.data);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static getUsers() {
        return new Promise ((resolve,reject) => {
            axios.get(url + "user").then((res) => {
                resolve(res.data);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static getImageHistory() {
        return new Promise ((resolve,reject) => {
            axios.get(url + "image/history").then((res) => {
                resolve(res.data);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static insertConfirmation(usernm, secretPW, base64data) {
        return new Promise ((resolve,reject) => {
            axios.post(url + "drunk/confirm", {
                user: usernm, 
                secret : secretPW,
                base64Img: base64data
            }).then((res) => {
                resolve(res);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static insertDrunkImage(base64Str) {
        return new Promise ((resolve,reject) => {
            axios.post(url + "image/add", {
                 "base64Img": base64Str
            }).then((res) => {
                resolve(res);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }




}

export default DataService;