<template>

  <div class="container h-100" v-if="isLoaded">
    <p class="h2 mb-4" style="font-weight:bold" v-if="!isDrunk">Nope :(</p>
    <p class="h1 mb-4" style="font-weight:bold" v-else>YASSSSSS NIPS IS DRUNK </p>


        <div v-if="!isDrunk">
          <p class="h5 mb-4" v-if="daysSinceDrunk == -1">It has been <b>a bloody long time</b> since nips was drunk</p>
          <p class="h5 mb-4" v-else-if="daysSinceDrunk == 0">It has been <b>less than a day</b> since nips was drunk</p>
          <p class="h5 mb-4" v-else>It has been <b>{{daysSinceDrunk}} days</b> since nips was drunk</p>
        </div>

        <!-- <div class="container" >
          <blockquote class="blockquote" style="border-left: 5px solid #eee">
          <p style="padding-left:4.5px"> For over a decade, Nips is an untamed beast who ....</p>
          <footer class="blockquote-footer">Samuel Squire Darbo</footer>
        </blockquote>
        </div> -->

        <div v-if="!isDrunk">
          <h2>3 people must confirm</h2>
          <img src="../assets/people.png" class="image" style="width:100px;height:100px"><br /><br />
          <h2>In an hour</h2>
          <img src="../assets/clock.png" class="image" style="width:100px;height:100px">
          <h1>=</h1>
          <img src="../assets/drunk.png" class="image" style="width:100px;height:100px">     
        </div>
        <br />
        

       <form class="text-center border border- p-5" action="#!" v-if="!isDrunk">

        <p class="h4 mb-4">Confirm nips is drunk now!</p>

       
          <select v-model="userSelected" class="form-control mb-4">
          <option v-for="u in users" v-bind:value="u.user" v-bind:key="u.user">
            {{ u.user }}
            </option>
          </select>


        <!-- Password -->
        <input type="password" class="form-control mb-4" placeholder="Enter your secret code.." v-model="secret">

        <label class="btn btn-secondary" for="my-file-selector">
        <input id="my-file-selector" type="file" class="d-none" @change="handleImageInput" accept="image/*">
        Upload photo evidence
        </label>
        
        <!-- <input @change="uploadImage" type="file" name="photo" accept="image/*"> -->
        <img :src="imageSrc" v-if="showImgThumbnail" id='formImgInput' class="image img-thumbnail" style="margin-top:10px;margin-bottom:10px;width: 120px; height: 120px;">  

    
        
        <p class="mb-4 h6" style="font-weight:bold;color:red" v-if="requiredError">Please enter a user, secret and photo</p>
        <button class="btn btn-success btn-block my-4" type="button" v-on:click="createConfirmation">Submit</button>
        
        <p class="mb-4 h5" style="font-weight:bold;color:green" v-if="showConfirmSuccess">Confirmed successful!</p>
        <p class="mb-4 h5" style="font-weight:bold;color:red" > {{confirmErrMessage }} </p>


    </form>
    <button id="my-button" type="submit" style=" height:1px;width:1px;background: transparent;border: none !important;font-size:0;"></button>
    <div v-if="isDrunk"> 
    <label class="btn btn-secondary" for="drunkInput">
        <input id="drunkInput" type="file" class="d-none" @change="handleImageInput" accept="image/*">
        Choose a drunk photo
        </label>
        
        <img :src="drunkImageSrc" v-if="showDrunkImgThumbnail" id='drunkImgInput' class="image img-thumbnail" style="margin-top:10px;margin-bottom:10px;width: 120px; height: 120px;">  
        
        <button class="btn btn-success btn-block my-4" type="button" v-on:click="insertDrunkImage">Submit</button>
        <p class="mb-4 h6" style="font-weight:bold;color:red" v-if="drunkImageError">Something went wrong, try again later.</p>
        <p class="mb-4 h6" style="font-weight:bold;color:green" v-if="drunkImageSuccess">Uploaded!</p>
        
       

           
            <p class="h4 mb-4"><b>Current drunk photos!</b></p>
            <section id="photos" class="pagination-centered">
              
              <img v-for="(image, imageIndex) in drunkImages" :key="imageIndex" :src="image.imageURL">
            </section>

          
        </div>

          <p class="h4 mb-4"><b>Past drunk photos</b></p>
            <section id="photos">
              <img v-for="(image, imageIndex) in imageHistory" :key="imageIndex" :src="image.imageURL">
            </section>
 
    </div>

  

</template>

<script>
import DataService from '../DataService';
import { confetti } from 'dom-confetti';


export default {
  name: 'DrunkMain',
  data () {
    return {
      confirms: [],
      error: '',
      user: '',
      image_base64: '',
      isDrunk: '',
      daysSinceDrunk: 0, 
      drunkImages: [],
      users: [],
      userSelected: '',
      secret: '',
      requiredError: false,
      imageSrc: '',
      showImgThumbnail: false,
      confirmErrMessage: '',
      showConfirmSuccess: false,
      response: [],
      imageHistory: [],
      isLoaded: false,
      drunkImageSrc: '',
      drunkImageSuccess: false,
      drunkImageError: false,
      showDrunkImgThumbnail: false,
      imageSizeTypeError: false,
      drunkImageSizeTypeError: false,
      index: null,
      imageSize: '',
      drunkImageSize: ''
    }

  },
    watch: {
    isDrunk: function () {
        if (this.isDrunk) { document.getElementById("my-button").click(); }
      
      }
    },
  methods: {
    async createConfirmation() {
      
    let base64data = '';
     try {
      if (this.showImgThumbnail) { base64data = document.getElementById("formImgInput").src; }

        this.requiredError = false;
        this.confirmErrMessage = '';
        this.showConfirmSuccess = false;

        if (this.userSelected =='' || this.secret == '' || base64data == '') { 
          this.requiredError = true;
          return;
        }
        
        this.response = await DataService.insertConfirmation(this.userSelected, this.secret, base64data);
        
        this.showConfirmSuccess = true;
        const responseDrunk =  await DataService.getDrunk();
        this.isDrunk = responseDrunk.isDrunk;

         if (!this.isDrunk) { this.daysSinceDrunk = responseDrunk.daysSinceDrunk  }
         else { this.drunkImages = responseDrunk.currentDrunkImgs }
         
         this.imageHistory = await DataService.getImageHistory()
        
        
      } catch(err) {
        this.confirmErrMessage = err.response.data;
      }
      
    },
     async uploadImage(e) {    
   
      var file = e.target.files[0];
     
      //alert(file['type']);
      // check if its an image > file['type'].split('/')[0]

      //var totalBytes = file.size;
      // var _size;
      // if(totalBytes < 1000000){
      //    _size = Math.floor(totalBytes/1000) + 'KB';
      //    // alert(_size);
      // }else{
      //    _size = Math.floor(totalBytes/1000000) + 'MB';  
      //   // alert(_size);
      // }

      //if Math.floor(totalBytes/1000000) < 8 then no error 

      var reader = new FileReader();
     
      reader.onload = function(e) {
        
         document.getElementById("formImgInput").src = e.target.result;
       //alert(e.target.result);        
      };
      reader.onerror = function(error) {
        alert(error);
      };
      reader.readAsDataURL(file); 
      
      this.showImgThumbnail = true;      
    },
   async handleImageInput(e) {    
   
      var file = e.target.files[0];    
      var drunkinputer = (e.target.id == "drunkInput")

       var totalBytes = file.size;
       var _size;
      if (totalBytes < 1000000){
         _size = Math.floor(totalBytes/1000) + 'KB';
         // alert(_size);
      } else{
         _size = Math.floor(totalBytes/1000000) + 'MB';  
        // alert(_size);
      }

      if (drunkinputer){
          this.drunkImageSize = _size;
      } else {
          this.imageSize = _size;
      }
      
      // must be less than 6mb as we are sending this data over http post.
      if (Math.floor(totalBytes/1000000) > 6 || file['type'].split('/')[0] != 'image'){
          if (drunkinputer){
              this.drunkImageSizeTypeError = true;
          } else {
               this.imageSizeTypeError = true;
          }
      }
        
      var reader = new FileReader(); 
      reader.onload = function(e) {

        if (drunkinputer){
           document.getElementById("drunkImgInput").src = e.target.result;
        } else {
          document.getElementById("formImgInput").src = e.target.result;
        }
        //alert(e.target.result);        
      };
      reader.onerror = function(error) {
        alert(error);
      };
      reader.readAsDataURL(file); 
      
       if (drunkinputer){
           this.showDrunkImgThumbnail = true;
        } else {
          this.showImgThumbnail = true;    
        }
      
    },
    async insertDrunkImage() {

      let base64data = '';
      try {
        if (this.showDrunkImgThumbnail) { base64data = document.getElementById("drunkImgInput").src; }

          this.drunkImageSuccess = false;
          this.drunkImageError = false;

          if (base64data == '') { return; }
          
          await DataService.insertDrunkImage(base64data);       
          this.drunkImageSuccess = true;

          const response =  await DataService.getDrunk();        
          this.isDrunk = response.isDrunk;

          if (!this.isDrunk) { this.daysSinceDrunk = response.daysSinceDrunk  }
          else { this.drunkImages = response.currentDrunkImgs }
          
        } catch(err) {
          this.drunkImageError = true;
        }



    }
    
  },
   mounted: async function() {
      const response =  await DataService.getDrunk();
      this.isLoaded = true;
      
      this.isDrunk = response.isDrunk;

      if (!this.isDrunk) { this.daysSinceDrunk = response.daysSinceDrunk  }
      else { this.drunkImages = response.currentDrunkImgs }

     this.users = await DataService.getUsers();
   
     const button = document.getElementById("my-button");
     button.addEventListener("click", () => confetti(button));

     if (this.isDrunk) { document.getElementById("my-button").click(); }


     this.imageHistory = await DataService.getImageHistory()

  

     

    },
}
</script>

